import{ createRouter,createWebHashHistory} from 'vue-router'

const batches = ()=>import('./components/batches.vue');
const login = ()=>import('./components/login.vue');
const stulist = ()=>import('./components/stuList.vue');
const courses = ()=>import('./components/courses.vue');
const classes = ()=>import('./components/classes.vue');
const teach = ()=>import('./components/teach.vue');


const routes = [
  {path:'/',redirect:'/login'},
  {path:'',redirect:'/login'},
  {path: '/batches', component: batches},//显示批次列表，上传老师文件
  {path: '/login', component: login},//管理员登录
  {path: '/stulist', component: stulist},//查看批次下学生列表，删除学生，上传学生文件
  {path: '/courses', component: courses},//显示某一批次下的课程列表，新建课程
  {path: '/classes', component: classes},//显示某一课程下的班级列表，新建班级
  {path: '/teach', component: teach},//显示某一班级下的授课列表，新建授课
]

const router = createRouter({
  history:createWebHashHistory(process.env.NODE_ENV),
  routes, 
})

import axios from 'axios';
import VueAxios from 'vue-axios'
axios.defaults.baseURL = 'https://zyz.hdu.edu.cn/';
// axios.defaults.baseURL = 'http://124.222.182.152:8080/demo_war/';

app.use(VueAxios,axios);
app.use(router);
app.use(vue3JsonExcel);

router.isReady().then(() => app.mount('#app'))